@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.fade-in {
  animation-name: fadeIn; }

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -30px, 0); }
  100% {
    opacity: 1;
    transform: none; } }

.fade-in-down {
  animation-name: fadeInDown; }

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translate3d(-30px, 0, 0); }
  100% {
    opacity: 1;
    transform: none; } }

.fade-in-left {
  animation-name: fadeInLeft; }

@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translate3d(30px, 0, 0); }
  100% {
    opacity: 1;
    transform: none; } }

.fade-in-right {
  animation-name: fadeInRight; }

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 30px, 0); }
  100% {
    opacity: 1;
    transform: none; } }

.fade-in-up {
  animation-name: fadeInUp; }
